<template>
  <div class="converter">
    <h1>Unit conversion utility</h1>
    <v-text-field
      v-model="wei"
      :counter="100"
      label="Wei"
      data-vv-name="wei"

      required
    ></v-text-field>
    <v-text-field v-model="gwei" :counter="100" label="Gwei" data-vv-name="gwei" required></v-text-field>
    <v-text-field v-model="ether" :counter="100" label="Ether" data-vv-name="ether" required></v-text-field>
  </div>
</template>

<script>
import Bignumber from 'bignumber.js'
Bignumber.config({ DECIMAL_PLACES: 80 })
const factor = {
  ether: 18,
  gwei: 9
}
function computedGet(v = 0, type){
  if(!v) {
    return '0'
  }
  const result = new Bignumber(v).dividedBy(new Bignumber(10).pow(factor[type])).toFixed()
  return result
}

function computedSet(newValue, oldValue, type){
  if(!newValue) {
    return '0'
  }
  const result = new Bignumber(newValue).times(new Bignumber(10).pow(factor[type]))
  if(result.isNaN()){
    return new Bignumber(oldValue).times(new Bignumber(10).pow(factor[type]))
  }
  return result.toFixed()
}

export default {
  props: {
    source: String
  },
  data: () => ({
    v: "1000000000000000000",
  }),
  computed: {
    ether: {
      get: function() {
        return computedGet(this.v, 'ether')
      },
      set: function(newValue) {
        this.v = computedSet(newValue, this.ether, 'ether')
      }
    },
    gwei: {
      get: function() {
        return computedGet(this.v, 'gwei')
      },
      set: function(newValue) {
        this.v = computedSet(newValue, this.ether, 'gwei')
      }
    },
    wei: {
      get: function() {
        return this.v 
      },
      set: function(newValue) {
        this.v = newValue
      }
    }
  },
  methods: {
    enter(e) {
      console.log(e);
    }
  },
  created() {}
};
</script>

